
import blog from '@/api/blog.js'

export default {
  data () {
    return {
      blogs: [],
      total: 0,
      page: 1
    }
  },
  created() {
    console.log(this.page)
    this.page = parseInt(this.$route.query.page) || 1 //这里的this.$route.queery
    blog.getIndexBlogs({ page: this.page }).then(res => {
      this.blogs = res.data
      this.total = res.total
      this.page = res.page
    })
  },
  methods: {
    onPageChange(newPage) {
      blog.getIndexBlogs({ page: newPage }).then(res => {
        this.blogs = res.data
        this.total = res.total
        this.page = res.page
        console.log(this.page)
        this.$router.push({ path: '/', query: { page: newPage } }) //这里的this.$router.push()方法不理解
      })
    }
  }
}



